#marquee {
  position: relative;
  height: 180px;
  background: #e75e26;
  white-space: nowrap;
  overflow: hidden;
}

#marquee-text {
  position: absolute;
  top: 50%;
  font-family: 'Oswald';
  font-size: 100px;
  color: #111111;
  text-transform: uppercase;
  margin: 0;
  -webkit-animation: marquee 27s infinite linear;
          animation: marquee 27s infinite linear;
}

@-webkit-keyframes marquee {
  from { -webkit-transform: translate(100vw, -50%); transform: translate(100vw, -50%); }
  to { -webkit-transform: translate(-100%, -50%); transform: translate(-100%, -50%); }
}

@keyframes marquee {
  from { -webkit-transform: translate(100vw, -50%); transform: translate(100vw, -50%); }
  to { -webkit-transform: translate(-100%, -50%); transform: translate(-100%, -50%); }
}



